<template>
  <div class="login-box">
    <div class="show-img"></div>
    <div class="form-box">
      <span class="text-sty">登录</span>
       <a-input v-model:value="loginForm.userId" placeholder="请输入账户名" />
       <a-input-password v-model:value="loginForm.password" placeholder="请输入密码" />
       <a-button type="primary" @click="login">登录</a-button>
    </div>
  </div>
</template>

<script>
import { defineComponent, reactive } from 'vue'
import { loginByForm } from '@/api/commonApi'
import { message } from 'ant-design-vue'
import router from '@/router'

export default defineComponent({
  setup () {
    const loginForm = reactive({
      userId: '',
      password: ''
    })

    const login = () => {
      if (loginForm.userId === '' || loginForm.password === '') {
        message.warn('账户或密码不能为空')
        return
      }
      loginByForm(loginForm).then(res => {
        if (res.success) {
          message.success('登录成功')
          localStorage.setItem('isLogin', res.key)
          localStorage.setItem('userName', 'Admin')
          router.push('/')
        } else {
          message.error('账号或密码错误')
        }
      })
    }

    return {
      loginForm,
      login
    }
  }
})
</script>

<style lang="less" scoped>
.login-box {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  .show-img {
    width: 50vw;
    height: 100vh;
    background: url('/src/assets/flower.svg') no-repeat center;
  }
  .form-box {
    max-width: 400px;
    min-width: 300px;
    height: 400px;
    padding-bottom: 200px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .text-sty {
      font-size: 20px;
      font-weight: 800;
    }
  }
}
</style>
